.mod_table {
	border-spacing: 0;

	td,
	th {
		border-bottom: rem-calc(1) solid $color_seashell;
	}

	tr {
		&.is-deactivated {
			td {
				color: $color_spun-pearl;
			}
		}
	}

	tbody tr:nth-child(2n) {
		background-color: transparent;
	}

	thead {
		background: transparent;
	}
}

.table_options {
	text-align: right;

	.material-icons {
		margin-right: 5px;

		&:hover {
			color: $color_monza;
			cursor: pointer;
		}

		&:last-child {
			margin-right: 0;
		}
	}
}