/**
 * COLORS (Naming suggestions under: http://chir.ag/projects/name-that-color/)
 **/

$color_white: #FFFFFF;
$color_seashell: #F1F1F1;
$color_athens-grey: #FAFAFB;
$color_mercury: #E5E5E5;
$color_alto: #D9D9D9;
$color_spun-pearl: #A7A7AC;
$color_rolling-stone: #737a83;
$color_shuttle-gray: #5e666f;
$color_scorpion: #5F5D5D;
$color_emperor: #555555;
$color_red-ribbon: #E60042;
$color_monza: #CE073F;
$color_shiraz: #ab063b;
$color_paprika: #81022B;
$color_barberry: #ECD717;
$color-black: #000000;
$color_mine-shaft: #333333;
$color_athens-gray: #F7F7F8;

/**
 * TYPOGRAPHY
 **/
$font-family-base: 'Source Sans Pro', Arial, sans-serif;

$font-weight-light: 300;
$font-weight-regular: 500;
$font-weight-bold: 700;


/**
 * SPACER
 **/
$spacerSizes: s,m,l;
$spacerPosition: bottom;
$spacerProperty: margin;

$spacer: (
	small: (
		s: 0px,
		m: 0px,
		l: 0px
	),
	medium: (
		s: 0px,
		m: 0px,
		l: 0px
	),
	large: (
		s: 0px,
		m: 0px,
		l: 0px
	)
);

/**
 * FOUNDATION
 **/

// 1. Global
// ---------

$global-font-size: 62.5%;
$global-width: rem-calc(2300);
$global-lineheight: 1.5;

$body-background: $color-white;
$body-font-color: $color-black;
$body-font-family: $font-family-base;
$body-antialiased: true;
$global-margin: 1rem;
$global-padding: 1rem;
$global-weight-normal: $font-weight-regular;
$global-weight-bold: $font-weight-bold;
$global-radius: 0;
$global-text-direction: ltr;
$global-flexbox: true;


// 2. Breakpoints
// --------------

$breakpoints: (
	small: 0,
	b-small: 650px,
	b-medium: 780px,
	medium: 950px,
	semi-large: 1076px,
	large: 1400px,
	b-x-large: 1450px,
	x-large: 1700px
);
$print-breakpoint: large;
$breakpoint-classes: (small b-small b-medium medium semi-large large b-x-large x-large);


// 3. The Grid
// -----------

$grid-row-width: $global-width;
$grid-column-count: 12;
$grid-column-gutter: (
	small: 20px,
	medium: 30px,
);
$grid-column-align-edge: true;
$block-grid-max: 8;