.reveal {
	border-radius: rem-calc(4);
	box-shadow: 0 1px 2px rgba(0,0,0,.05), 0 0 0 1px rgba(63,63,68,.1);
	width: 93%;
	top: rem-calc(100) !important;
	height: auto !important;
	min-height: 0 !important;
	margin-right: auto !important;
	margin-left: auto !important;
	overflow-y: auto !important;
	padding: 0 !important;
	border: none !important;

	@include breakpoint(semi-large) {
		width: rem-calc(1010);
	}

	&.var_narrow {
		width: 93%;

		@include breakpoint(b-medium) {
			width: rem-calc(640);
		}
	}

	&:focus {
		outline: none;
	}
}

.reveal_header {
	display: flex;
	justify-content: space-between;
	background-color: $color_monza;
	padding: rem-calc(15 16 0);

	h1 {
		font-size: rem-calc(22);
		font-weight: $font-weight-regular;
		margin: rem-calc(0 0 11 14);
		color: $color_white;

		@include breakpoint(b-medium) {
			font-size: rem-calc(26);
		}
	}

	.material-icons {
		padding-right: rem-calc(9);
		font-size: rem-calc(32);
		margin-top: rem-calc(2);
		cursor: pointer;
		color: $color_white;
	}
}

.reveal_content {
	padding: rem-calc(24 16 31);
}

.reveal_add_product_to_order {
	text-align: center;
	margin-top: rem-calc(24);

	.material-icons {
		font-size: rem-calc(50);
		cursor: pointer;
	}
}