body {
	font-size: rem-calc(18);
	font-weight: $font-weight-regular;
	line-height: (21 / 18);
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
	background-color: $color_athens-gray;

	&.login {
		height: 100%;
	}
}

html {
	&.login {
		height: 100%;
	}
}

h1 {
	font-size: rem-calc(38);
	font-weight: $font-weight-bold;
	line-height: 1.5;
	margin: 0 0 pxToEm(40, 40);
}

h2 {
	font-size: rem-calc(30);
	font-weight: $font-weight-light;
	color: $color_mine-shaft;
	line-height: 1.5;
	margin: 0 0 rem-calc(15);


	&.var_heading {
		font-weight: $font-weight-light;
		line-height: 1.3;
		margin: 0 0 pxToEm(7.5, 30) 0;
	}
}

h3 {
	font-size: rem-calc(20);
	font-weight: $font-weight-bold;
	line-height: 1.5;
	margin: 0 0 pxToEm(20, 20);
}

h4 {
	font-size: rem-calc(16);
	font-weight: $font-weight-bold;
	line-height: 1.5;
	margin: 0;
}

i, em {
	font-style: italic;
}

b, strong {
	font-weight: $font-weight-bold;
}

p {
	margin: 0 0 pxToEm(16, 16);
}

img[src$=".svg"] {
	width: 100%;
}

a {
	text-decoration: none;
	color: inherit;

	&:hover {
		text-decoration: underline;
	}
}

button {
	line-height: unset;
	cursor: pointer;
}

.text-right {
	text-align: right;
}

.offset-top {
	margin-top: rem-calc(25) !important;
}

.offset-top-big {
	margin-top: rem-calc(40) !important;
}

.offset-bottom-small {
	margin-bottom: rem-calc(10);
}

.offset-bottom-big {
	margin-bottom: rem-calc(20);
}

.offset-right-small {
	margin-right: rem-calc(10);
}

.offset-left-small {
	margin-left: rem-calc(10);
}

.material-icons {
	&:focus {
		outline: none;
	}
	&.var_activated {
		color: $color_barberry;
	}
}

.pull-right {
	float: right;
}