.mod_navigation {

}

.navigation_section {
	border-bottom: rem-calc(2) solid $color_monza;

	ul {
		list-style: none;
		padding: 0;
		margin-top: rem-calc(15);

		li {
			position: relative;
			display: block;
			color: $color-white;
			font-size: rem-calc(20);
			margin-bottom: rem-calc(5);

			&.is-active,
			&:hover {
				a {
					background: rgba(123, 3, 38, 0.17);
					opacity: 1;
					text-decoration: none;
				}
			}

			a {
				position: relative;
				display: block;
				margin: rem-calc(0 22);
				padding: rem-calc(0 15);
				border-radius: 4px;
				opacity: .86;
				height: rem-calc(60);

				@include breakpoint(semi-large) {
					margin: rem-calc(0 17);
					padding: rem-calc(10 15);
					height: auto;
				}
			}

			p {
				margin: 0;
				display: none;

				@include breakpoint(semi-large) {
					display: block;
				}
			}

			.material-icons {
				font-size: rem-calc(36);
				margin: rem-calc(10 0 0 0);
				transition: font-size 0.6s ease;

				@include breakpoint(semi-large) {
					float: left;
					text-align: center;
					width: rem-calc(30);
					font-size: rem-calc(24);
					margin: rem-calc(0 15 0 0);
				}
			}
		}
	}

	&.profile {
		padding: rem-calc(20 0);
	}
}

.navigation_section_title {
	padding: rem-calc(14 35 0);

	h4 {
		color: $color_paprika;
		font-size: rem-calc(15);
		text-transform: uppercase;
		text-align: center;

		@include breakpoint(semi-large) {
			text-align: left;
		}
	}
}

.navigation_section_profile-image {
	margin-bottom: rem-calc(11);

	img {
		max-width: rem-calc(70);
		display: block;
		margin: auto;
		position: relative;
		border-radius: 100%;

		@include breakpoint(semi-large) {
			max-width: rem-calc(80);
		}
	}
}

.navigation_section_profile-description {
	text-align: center;
	color: $color_white;
}

.navigation_section_profile-description-name {
	display: none;

	@include breakpoint(semi-large) {
		display: block;
	}
}