$sidebar_width: rem-calc(110);
$sidebar_width_medium: rem-calc(231);

body {
	margin: 0;
}
.innerWrap {
	-webkit-flex: 1 1 auto;
	-ms-flex: 1 1 auto;
	flex: 1 1 auto;
	display: -webkit-flex;
	display: -ms-flexbox;
	display: flex;
	position: relative;
}
.dashboard {
	visibility: hidden;
	width: 0;

	@include breakpoint(b-medium) {
		visibility: visible;
		background-color: $color_red-ribbon;
		width: $sidebar_width;
		transition: all 0.6s ease;
		position: absolute;
		height: 100%;
		left: 0;
		top: 0;
		overflow-x: visible;
		overflow-y: auto;
	}

	@include breakpoint(semi-large) {
		width: $sidebar_width_medium;
	}

}

.content {
	padding: rem-calc(40 18);
	transition: all 0.6s ease;
	overflow-y: auto;
	-webkit-flex: 1 1 0;
	-ms-flex: 1 1 0;
	flex: 1 1 0;

	@include breakpoint(b-medium) {
		margin-left: $sidebar_width;
		padding: rem-calc(20);
	}

	@include breakpoint(semi-large) {
		margin-left: $sidebar_width_medium;
	}
}

.navbar {
	width: 100%;
	background-color: #7488d8;
}