.mod_mobile-navigation {
	display: block;

	&.is-hidden {
		display: none;
	}

	@include breakpoint(b-small down) {
		background-color: $color_red-ribbon;

		ul {
			list-style: none;
			padding-left: rem-calc(29);
		}

		li {
			a {
				display: flex;
				justify-content: flex-start;

				&:hover {
					text-decoration: none;
				}
			}

			.material-icons {
				font-size: rem-calc(32);
				color: $color_white;
				margin-right: rem-calc(20);
			}

			p {
				display: inline-block;
				font-size: rem-calc(26);
				color: $color_white;
			}
		}
	}
}

@include breakpoint(b-small down) {

	.mobile-navigation_section_title {
		width: 50%;
		margin: rem-calc(10) auto 0;

		h4 {
			font-size: rem-calc(20);
			color: $color_paprika;
			text-transform: uppercase;
			text-align: center;
		}
	}
}