.mod_card {
	position: relative;
	background-color: $color_white;
	border-radius: rem-calc(4);
	box-shadow: 0 1px 2px rgba(0, 0, 0, 0.05), 0 0 0 1px rgba(63, 63, 68, 0.1);
	padding-bottom: rem-calc(30);

	&:after {
		content: "";
		display: table;
		clear: both;
	}

	h3 {
		font-size: rem-calc(17);
		font-weight: $font-weight-regular;
		margin-bottom: rem-calc(30);
	}

	&.var_highlight {
		padding: rem-calc(25 25 10 25);
		position: relative;

		.card_category-border {
			position: absolute;
			width: 100%;
			height: rem-calc(7);
			top: 0;
			left: 0;
			background-color: $color_red-ribbon;
		}

		.material-icons {
			float: left;
			font-size: rem-calc(45);
			margin-left: 0;
			margin-right: rem-calc(25);
			margin-top: 0;
		}

		h3,
		p {
			overflow: hidden;
			font-size: rem-calc(16);
		}

		h3 {
			font-weight: $font-weight-bold;
			margin-bottom: 0;
			text-transform: uppercase;
		}

		&:after {
			clear: both;
			display: table;
			content: ""
		}
	}
}

.card_header {
	display: flex;
	justify-content: space-between;
	padding: rem-calc(15 0 0);
	background-color: $color_shuttle-gray;
	margin-bottom: rem-calc(20);

	h2 {
		text-transform: uppercase;
		font-size: rem-calc(18);
		font-weight: $font-weight-regular;
		padding-left: rem-calc(30);
		margin: rem-calc(0 0 11);
		color: $color_white;

		@include breakpoint(b-small) {
			font-size: rem-calc(22);
		}

		&.var_additional {
			padding-right: rem-calc(30);
			padding-left: 0;
			color: $color_athens-grey;
		}
	}
}

.card_content {
	padding: rem-calc(5 30 25);
}

.card_list_section_row {
	display: flex;
	justify-content: space-between;

}

.card_list {
	margin-bottom: rem-calc(20);
}

.card_list_options {
	color: $color_monza;

	.material-icons {
		&:hover {
			color: $color_paprika;
			cursor: pointer;
		}
	}
}

.card_list_section {
	margin-bottom: rem-calc(20);
}

.card_option {
	margin-top: rem-calc(25);

	.mod_button {
		float: right;
	}

	&:after {
		clear: both;
		display: table;
		content: "";
	}
}

.card_options {
	text-align: right;
}