.mod_logo {
	background-color: $color_red-ribbon;
	width: rem-calc(110);
	transition: width 0.6s ease;
	float: left;

	@include breakpoint(b-medium) {
		border-bottom: rem-calc(2) solid $color_monza;
	}

	@include breakpoint(semi-large) {
		width: rem-calc(231);
	}

	h1 {
		font-size: rem-calc(30);
		color: $color_white;
		padding: rem-calc(8 30 8 42);
		margin: 0;

		@include breakpoint(semi-large) {
			padding: rem-calc(8 30 8 35);
			font-size: rem-calc(38);
		}

		&.var_small {
			padding: rem-calc(13 30 13 42);
		}
	}
}