.mod_header {
	display: none;
	background-color: $color-white;

	@include breakpoint(b-medium) {
		display: block;
		width: 100%;
		position: relative;
	}

	&.var_mobile {
		display: block;
		background-color: $color_red-ribbon;

		@include breakpoint(b-medium) {
			display: none;
		}

		&:after {
			clear: both;
			display: table;
			content: "";
		}

		.mod_logo {
			float: left;
			padding: rem-calc(5 30 5 32);

			@include breakpoint(semi-large) {
				padding: rem-calc(7 30 7 32);
			}

			h1 {
				padding: 0;
			}
		}

		.material-icons {
			font-size: rem-calc(35);
			float: right;
			margin: rem-calc(12 26 0);
			color: $color-white;
			cursor: pointer;

			@include breakpoint(semi-large) {
				font-size: rem-calc(40);
				margin: rem-calc(19 26 0);
			}
		}
	}
}

.header_inner {
	display: none;

	@include breakpoint(b-medium) {
		display: block;
		width: 100%;
		padding: rem-calc(13 28 0 140);

		h2 {
			width: 70%;
			display: inline-block;
		}

		.material-icons {
			font-size: rem-calc(35);
		}
	}

	@include breakpoint(semi-large) {
		padding: rem-calc(13 35 0 264);
	}
}

.header_icons {
	float: right;
	margin-top: rem-calc(6);
	cursor: pointer;
}