.mod_quicknav {
	width: 100%;
	padding: rem-calc(19 31);
	background-color: $color_shuttle-gray;
	color: $color_white;
	display: flex;
	justify-content: space-between;

	@include breakpoint(b-medium) {
		display: none;
	}
}

.quicknav_info {
	font-size: rem-calc(20);
}

.quicknav_icons {
	.material-icons {
		font-size: rem-calc(30);
		cursor: pointer;
		margin-top: rem-calc(-3);

		&.open-mobile-shopping-basket,
		&.close-mobile-shopping-basket {
			margin: rem-calc(0 0 0 20);

			&.is-hidden {
				display: none;
			}
		}
	}
}