.mod_select-2 {
	input {
		height: auto;
		margin: 0;
		padding: rem-calc(5);
		border-radius: 0;

		&[type=submit] {
			font-size: rem-calc(18);
			background-color: $color_monza;
			color: $color_white;
			padding: rem-calc(8 11);
			border-radius: rem-calc(2);
			margin-top: rem-calc(11);
			border-color: $color_monza;
			cursor: pointer;
		}
	}

	.select-2_select {
		&:focus {
			outline: none !important;
		}
	}

	.select2-container {
		margin-top: rem-calc(10);
	}

	.select2-selection {
		border-radius: 0 !important;
	}

	.select2-container .select2-selection--multiple,
	.select2-container--default .select2-selection--single {
		min-height: rem-calc(45);
		border: rem-calc(1) solid $color_rolling-stone !important;
	}

	.select2-container--default .select2-selection--single .select2-selection__rendered {
		min-height: rem-calc(45);
		margin-top: rem-calc(7);
	}

	.select2-selection__arrow {
		right: rem-calc(12) !important;
		top: rem-calc(9) !important;
	}

	label {
		font-size: rem-calc(20);

		input {
			font-size: rem-calc(20);
			margin-top: rem-calc(10);
			padding: rem-calc(10);
			text-align: center;
			border: rem-calc(1) solid $color_rolling-stone;
		}
	}
}

.select2-container {
	width: 100% !important;
}
