.mod_login {
	width: 90%;
	margin: 0 auto;
	background: $color_white;
	padding: rem-calc(35 52 61);
	border-radius: rem-calc(4);
	border-top: rem-calc(5) solid $color_monza;

	@include breakpoint(b-small) {
		width: rem-calc(600);
	}

	label {
		text-transform: uppercase;
		font-size: rem-calc(16);
		color: $color_emperor;

		&.var_no-text-transform {
			text-transform: none;
		}
	}

	input {
		display: block;
		width: 100%;
		height: rem-calc(55);
		font-size: rem-calc(18);
		line-height: 1.2;
		color: $color_emperor;
		margin-top: rem-calc(8);
		background: transparent;
		padding: rem-calc(0 25);

		&[type=checkbox] {
			display: inline;
			height: auto;
			width: auto;
			padding: 0;
		}
	}

	a {
		&.var_grey {
			color: $color_emperor;
		}

		&.pull-right {
			float: none;
			font-size: rem-calc(16);
			margin: rem-calc(10 0 2 0);
			display: inline-block;

			@include breakpoint(b-small) {
				float: right;
				margin: rem-calc(6 0 0 0);
			}
		}
	}

	.mod_button {
		display: inline-block;
		max-width: rem-calc(140);
		width: 100%;
		text-align: center;
		margin-top: rem-calc(23);
	}
}

.login_header {
	padding: rem-calc(0 15);

	h1 {
		color: $color_emperor;
		font-size: rem-calc(30);
		margin-bottom: rem-calc(30);
	}
}

.login_status {
	margin-bottom: rem-calc(30);
	color: $color_monza;
}