.mod_button {
	font-size: rem-calc(18);
	background-color: $color_monza;
	color: $color_white;
	padding: rem-calc(8 11);
	border-radius: rem-calc(2);

	&.var_submit {
		border: none;
		padding: rem-calc(8 21);
	}

	&.var_grey {
		color: $color_white;
		background: $color_rolling-stone;

		&:hover {
			background-color: $color_shuttle-gray;
		}
	}

	&:hover {
		text-decoration: none;
		background-color: $color_shiraz;
	}

	&:focus {
		outline: none;
	}
}