.mod_list {
	margin-bottom: rem-calc(35);

	.material-icons {
		cursor: pointer;
	}
}

.list_row {
	display: flex;
	justify-content: space-between;
	width: rem-calc(240);
	margin-bottom: rem-calc(5);
}

.list_item {
	font-size: rem-calc(18);
}

.list_icons {
	text-align: right;
	color: $color_monza;

	.material-icons {
		margin-right: rem-calc(5);

		&:hover {
			color: $color_paprika;
			cursor: pointer;
		}
	}
}

.list_detail {
	display: block;
	position: absolute;
	z-index: 1;
	top: rem-calc(20);
	right: rem-calc(19);
	padding: rem-calc(22 28);
	border-radius: 4px;
	box-shadow: 0 1px 2px rgba(0, 0, 0, 0.05), 0 0 0 1px rgba(63, 63, 68, 0.1);
	background-color: $color_white;
	border-top: rem-calc(4) solid $color_red-ribbon;
}

.list_detail-header {
	margin-bottom: rem-calc(15);
}

.list_detail_header-title {
	font-size: rem-calc(20);
	font-weight: $font-weight-bold;
}

.list_detail-item,
.list_detail-header {
	display: flex;
	justify-content: space-between;
}

.list_detail_item-name {
	padding: rem-calc(10 40 10 0);
}

.list_detail_item-price {
	padding: rem-calc(10 0 10 40);
}