.mod_form {
	label {
		font-size: rem-calc(20);
	}

	input {
		box-shadow: none;
		margin-top: rem-calc(2);
		font-size: rem-calc(18);

		@include placeholder {
			font-size: rem-calc(16);
		}

		&[type=file] {
			width: rem-calc(10);
		}
	}

	select {
		font-size: rem-calc(18);
	}
}

.form_upload {
	font-size: rem-calc(18) !important;
	margin-top: rem-calc(38);
	padding: rem-calc(3 16) !important;
}